import { PPNDealer, PPNDealerSearchResult } from '../../entities/dealer-response';
import { EventEmitter, h, JSX } from '@stencil/core';
import { isString } from '../../utility/helper';
import { getDealerDetailsRoutingKey, RoutingKeys } from '../../entities/routing-key';
import { dealerSearchResultsStore } from '../../state/dealer-search-results-store';
import globalDataLayerService from '../../services/analytics/global-data-layer-service';
import {
  ClickElementTypes,
  DEALER_SEARCH_DISPLAY_ELEMENT_ID,
  ElementTypes,
  EventActions
} from '../../utility/constants';
import analyticsManager from '../../services/analytics/analytics-manager';
import { navContentStore } from '../../state/nav-content-store';
import { splitLocale } from '../../entities/locale';

export function DealerSearchFormItemCard({
  dealer,
  showOverlay,
  eventEmitter,
  locale
}: {
  dealer: PPNDealerSearchResult;
  showOverlay?: boolean;
  eventEmitter: EventEmitter;
  locale: string;
}): JSX.Element | null {
  const { ppnDealer, distance } = dealer;
  const language = splitLocale(locale).language;
  const isAddressLocalized = Object.values(ppnDealer.addressLocalized).every((x) => x != null && x != '');
  // postalCode is currently not available in addressLocalized
  const address =
    language === 'en' ? ppnDealer.address : isAddressLocalized ? ppnDealer.addressLocalized : ppnDealer.address;
  const name =
    language === 'en' ? ppnDealer.name : isString(ppnDealer.nameLocalized) ? ppnDealer.nameLocalized : ppnDealer.name;
  const numberOfDealers = navContentStore.state.dealer.numberOfDealers;
  let country = '';
  try {
    country = splitLocale(locale).country;
  } catch (err) {
    // throw the error away
  }
  const onDealerCardClick = (ppnDealer: PPNDealer): void => {
    const dealerDetailsRoutingKey = getDealerDetailsRoutingKey(ppnDealer.id);
    eventEmitter.emit([dealerDetailsRoutingKey]);
    const { dealer: dealerContent } = navContentStore.state;
    const { results, searchTerm } = dealerSearchResultsStore.state;

    globalDataLayerService.pushDataLayerEvent(EventActions.DEALER_RESULT_CLICK, {
      componentClick: {
        clickElementId: ppnDealer.id,
        clickElementName: ppnDealer.name,
        clickElementType: ClickElementTypes.Button,
        targetElement: `level2_${dealerDetailsRoutingKey}`
      },
      componentDisplay: {
        displayElementId: DEALER_SEARCH_DISPLAY_ELEMENT_ID,
        displayElementType: analyticsManager.calculateDisplayElementType(
          RoutingKeys.DEALER_SEARCH,
          ElementTypes.dealerSearchForm
        ),
        displayElementName: dealerContent.text
      },
      componentSearch: {
        searchTerm: searchTerm,
        numberOfResults: results?.length
      },
      partner: {
        companyName: ppnDealer.name,
        companyId: ppnDealer.porschePartnerNo
      }
    });
  };

  return (
    <phn-menu-item-card
      tabIndex={0}
      text={name}
      class={`animated ${showOverlay ? 'dealer-card-overlay' : ''} dealer-card`}
      onClick={() => onDealerCardClick(ppnDealer)}
      analyticsId={ppnDealer.id}
    >
      <div class="phn-flex-row phn-justify-between phn-align-center">
        {country === 'JP' ? (
          <phn-p-text color="primary" size="small">
            {`ã€’${ppnDealer.address.postalCode} ${address.city} ${address.street}`}
          </phn-p-text>
        ) : (
          <phn-p-text color="primary" size="small">
            {`${address.street}, `}
            <span class="city">
              {ppnDealer.address.postalCode} {address.city}
            </span>
          </phn-p-text>
        )}

        {distance && distance.value >= 1 && numberOfDealers > 5 ? (
          <phn-p-tag color="notification-info-soft">{`${Math.round(
            distance.value
          )}${distance.unit.toLowerCase()}`}</phn-p-tag>
        ) : null}
      </div>
    </phn-menu-item-card>
  );
}
