@import '../../scss/helpers';

:host {
  display: block;

  phn-dealer-details-drawer{
    margin-top: p-px-to-rem(20px);
  }


  phn-menu-item-card {
    & > div {
      margin-top: 14px;
    }
  }

  .dealer-card-overlay {
    opacity: 0.3;
  }

  .dealer-card{
    phn-p-tag{
      margin-left: p-px-to-rem(16px);
    }
  }

  .error-message {
    position: relative;
    margin-top: p-px-to-rem(32px);
  }

  .animated {
    animation: fadeInFromBottom 0.75s ease-in-out 0s;
  }

  a {
    display: inline-block;
    position: relative;
    width: 100%;
    text-decoration-line: none;
    margin-top: p-px-to-rem(36px);

    phn-p-button {
      width: 100%;
    }
  }
}
